.comment {
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  align-content: space-between;
  flex-direction: row;
  justify-content: center; }
  .comment .wrapper {
    flex: 0 1 100%;
    display: flex; }
  .comment button {
    border: 0;
    background: #616161;
    color: #fff;
    border-radius: 8px;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0; }
  .comment textarea {
    width: 100%;
    min-height: 64px;
    resize: none;
    border-radius: 8px;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0; }
  .comment p {
    flex: 0 1 100%; }
  .comment .list {
    flex: 0 1 100%; }
    .comment .list .value {
      box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
      padding: 8px;
      text-align: left;
      padding-left: 24px;
      margin-bottom: 10px;
      display: flex;
      justify-content: space-between; }
      .comment .list .value button {
        border-radius: 8px; }
